(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('eventShow', eventShow);

  /** @ngInject */
  function eventShow(events) {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/pages/events/show/show.html',
      controller: EventShowController,
      controllerAs: 'eventShow',
      bindToController: true,
      replace: true,
      link: EventShowLink,
      scope: {
        event: '=eventShow'
      }
    };

    return directive;

    /** @ngInject */
    function EventShowController(models, $uibModal) {
      var vm = this;

      vm.destroy = function() {

        $uibModal.open({
            templateUrl: 'app/pages/events/destroy/destroy.html',
            controller: 'EventDestroyController',
            controllerAs: 'eventDestroy',
            windowClass: 'modal-danger'
          }).result
          .then(function (res) {
            if(res === true){
              events.destroy(vm.event);
            }
          })
      }

      vm.models = models;
    }

    /** @ngInject */
    function EventShowLink(scope, el, attrs, vm){
      el.hover(
        function () {
          scope.$apply(function () {
            vm.showControls = true;
          })
        },
        function () {
          scope.$apply(function () {
            vm.showControls = false;
          })
        }
      );
      angular.element('#event-edit-form-' + vm.event.evenId).on('show.bs.collapse', function () {
        events.show(vm.event.evenId); // Request additional info about the event
        events.listRegions(vm.event.evenId); // We need regions
      });
    }
  }

})();

