(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('eventEdit', eventEdit);

  /** @ngInject */
  function eventEdit() {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/pages/events/edit/edit.html',
      controller: EventEditController,
      controllerAs: 'eventEdit',
      link: EventEditLink,
      bindToController: true,
      replace: true,
      scope: {
        event: '=eventEdit'
      }
    };

    return directive;

    /** @ngInject */
    function EventEditController(models, events, matches, $uibModal) {
      var vm = this;

      vm.models = models;

      vm.submit = function () {
        return events
          .form2Event(vm.form)
          .then(function (eventData) {
            return events.update(vm.event.evenId, eventData)
          });
      }

      vm.recreateCalendar = function () {
        $uibModal.open({
          templateUrl: 'app/pages/events/edit/recreateCalendar.html',
          controller: 'EventRecreateCalendarController',
          controllerAs: 'eventRecreateCalendar',
          windowClass: 'modal-warning'
        }).result
        .then(function (res) {
          if(res === true){
            return matches
            .createCalendar(vm.event.evenId)
            .then(function () {
              return events.show(vm.event.evenId);
            });
          }
        })

      }
    }

    function EventEditLink(scope, el, attrs, vm) {
      vm.cancel = function () {
        el.find('.accordion-body').collapse('hide');
      }

      scope.$watch('eventEdit.event.evenCurrentDay', function (newVal, oldVal) {
        if(angular.isDefined(newVal) && newVal !== oldVal){
          vm.form.currentDay = vm.event.evenCurrentDay;
        }
      });
    }
  }

})();

