'use strict';

angular.module('frispo')
  /**
   * A wizard
   */
  .factory('Wizard', function () {
    var defaults = {
      totalSteps: 1,
      startFromStep: 1
    }

    return function Wizard (options) {
      this.options = angular.merge(defaults, options);

      this.totalSteps = this.options.totalSteps;
      this.currentStep = this.options.startFromStep;

      this.next = function () {
        if(this.currentStep < this.totalSteps){
          this.currentStep++;
        }
      }

      this.prev = function () {
        if(this.currentStep > 1){
          this.currentStep--;
        }
      }
    };
  });

'use strict';

angular.module('frispo')
  /**
   * Utilities functions.
   */
  .factory('utils', function () {
    var utils = {
      str2Date: function (str) {
        var res = [];
        var range = str.split('->');
        angular.forEach(range, function (date) {
          var split = date.split('/');
          if(split.length !== 3) return null;
          res.push(split[2].trim() + '-' + split[1].trim() + '-' + split[0].trim())
        })
        if(res.length === 1){
          return res[0];
        }
        else{
          return res;
        }
      },
      date2Str: function (dateStart, dateEnd) {
        if(!dateStart) return null;

        var split = dateStart.split('-');
        if(split.length !== 3) return null;
        var res = split[2].trim() + '/' + split[1].trim() + '/' + split[0].trim();

        if(dateEnd){
          res +=  ' -> ' + utils.date2Str(dateEnd);
        }
        return res;
      },
      str2Time: function (str) {
        var res = null;
        var splitted = str.trim().split(' ');
        if(splitted.length == 2){
          var hm = splitted[0].trim().split(':');
          var pmAdd = (splitted[1] == 'PM' ? 12 : 0);
          if(hm.length == 2){
            res = (+hm[0] + pmAdd) + ':' + hm[1];
          }
        }
        return res;
      }
    };

    return utils;
  });

