'use strict';

angular.module('frispo')
  /**
   * Long Term Memory
   * Persist data to make it available after the browser is closed
   */
  .factory('longTermMemory', function () {
    var allowedKeys = [
      'currentUser',
      'events',
      'regions',
      'currentEvent'
    ];

    var longTermMemory = {
      memorize: function(key, val) {
        if(allowedKeys.indexOf(key) < 0){
          return;
        }

        localStorage[key] = angular.toJson(val);
      },

      remember: function(key) {
        if(allowedKeys.indexOf(key) < 0){
          return null;
        }

        try {
          return angular.fromJson(localStorage[key]);
        }
        catch(e){
          delete localStorage[key];
          return null;
        }
      },

      forget: function(key) {
        if(allowedKeys.indexOf(key) < 0){
          return null;
        }

        delete localStorage[key];
      },

      oblivion: function() {
        angular.forEach(allowedKeys, function(key) {
          delete localStorage[key];
        });
        localStorage.clear();
      }
    };

    return longTermMemory;
  });

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('frispoSidebar', frispoSidebar);

  /** @ngInject */
  function frispoSidebar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/frispoSidebar/frispoSidebar.html',
      controller: SidebarController,
      controllerAs: 'sidebar',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function SidebarController(models, $state) {
      var vm = this;

      vm.models = models;
      vm.state = $state;

      vm.manageMenuEntries = [
        'main.admin.createEvent',
        'main.admin.events',
        'main.admin.teams',
        'main.admin.calendar',
        'main.admin.scoreboard'
      ]
    }
  }

})();

