'use strict';

angular.module('frispo')
  /**
   * My Project Overview API service
   */
   .factory('frispoApi', function ($q, $http, usersApiUrl, eventsApiUrl, teamsApiUrl, matchesApiUrl, consumerKey, consumerSecret, models) {
      // Cache the promises (just to avoid to call the same API before the previous has finished)
      var cache = {};

      // Api Helpers (accessible only from this factory)
      var helpers = {
        /**
         * Try to retrieve the result from cache, or make HTTP call
         * @param  {String} method  The HTTP method
         * @param  {String} fullUrl The API url
         * @param  {Object} data    The body
         * @param  {Object} headers The headers
         * @param  {Boolean} keep   If true, the promise is cached even after it's been fulfilled
         * @return {Promise}        A promise of the response
         */
        cacheOrHttp: function(method, fullUrl, data, headers, keep) {
          var key = method + fullUrl + (data ? angular.toJson(data) : '');

          var config = {};
          if(method) config.method = method.toUpperCase();
          if(fullUrl) config.url = fullUrl;
          if(data) config.data = data;
          if(headers) config.headers = headers;

          if (!cache[key]) {
            var promise;
            var err = false;
            switch(method){
              case 'get':
              case 'GET':
              case 'post':
              case 'POST':
              case 'put':
              case 'PUT':
              case 'patch':
              case 'PATCH':
              case 'delete':
              case 'DELETE':
                promise = $http(config);
                break;
              default:
                err = true;
                promise = $q.reject();
                break;
            }
            if(!err){
              cache[key] = promise.finally(function(response,err) {
                if(!keep){
                  delete cache[key]; // delete the cache, so the next call will be fresh
                }
                if(err) { return $q.reject(err); }
                if(response) { return response; }
              });
            }
          }

          return cache[key];
        },

        /**
         * Perform an API call to the Users endpoint, using preset headers and required data
         * @param  {String} method  The HTTP method
         * @param  {String} url The API url
         * @param  {Object} data    The body
         * @param  {Object} headers The headers
         * @return {Promise}        A promise of the response
         */
        userApiCall: function (method, url, data, headers) {
          url = url || '/';
          return helpers.cacheOrHttp(
            method,
            usersApiUrl + url,
            angular.merge({
                consumer_key: consumerKey,
                consumer_secret: consumerSecret
              },
              data
            ),
            angular.merge({
                Authorization: 'Basic ZnJpc3BvOmZyMXNwMA==' // frispo:fr1sp0
              },
              headers
            )
          );
        },

        /**
         * Perform an API call to the Events endpoint, using preset headers
         * @param  {String} method  The HTTP method
         * @param  {String} url     The API url
         * @param  {Object} data    The body
         * @param  {Object} headers The headers
         * @return {Promise}        A promise of the response
         */
        eventsApiCall: function (method, url, data, headers, keep) {
          url = url || '/';
          return helpers.cacheOrHttp(
            method,
            eventsApiUrl + url,
            data,
            angular.merge({
                Authorization: 'Bearer ' + models.currentUser.accessToken
              },
              headers
            ),
            keep
          );
        },

        /**
         * Perform an API call to the Teams endpoint, using preset headers
         * @param  {String} method  The HTTP method
         * @param  {String} url     The API url
         * @param  {Object} data    The body
         * @param  {Object} headers The headers
         * @return {Promise}        A promise of the response
         */
        teamsApiCall: function (eventId, method, url, data, headers) {
          url = url || '/';
          return helpers.cacheOrHttp(
            method,
            teamsApiUrl + '/events/' + eventId + '/teams' + url,
            data,
            angular.merge({
                Authorization: 'Bearer ' + models.currentUser.accessToken
              },
              headers
            )
          );
        },

        /**
         * Perform an API call to the Match endpoint, using preset headers
         * @param  {String} method  The HTTP method
         * @param  {String} url     The API url
         * @param  {Object} data    The body
         * @param  {Object} headers The headers
         * @return {Promise}        A promise of the response
         */
        matchesApiCall: function (eventId, method, url, data, headers) {
          url = url || '/';
          return helpers.cacheOrHttp(
            method,
            matchesApiUrl + '/event/' + eventId + url,
            data,
            angular.merge({
                Authorization: 'Bearer ' + models.currentUser.accessToken
              },
              headers
            )
          );
        },

        /**
         * Expect a 200 response and return data.
         * Otherwise, return a rejected promise.
         */
        expect200: function (response) {
          if(response.status === 200){
            return response.data;
          }
          else{
            return $q.reject(response);
          }
        },

        unwrapData: function (response) {
          return response.data;
        }
      };

      var frispoApi = {
        /** USER API **/

        /**
         * Register a new user
         */
        register: function(userData) {
          return helpers
            .userApiCall('post', '/create', {
              email: userData.email,
              password: userData.password
            })
            .then(helpers.expect200);
        },

        /**
         * Authenticate the user
         */
        login: function(userData) {
          return helpers
            .userApiCall('post', '/login', {
              email: userData.email,
              password: userData.password
            })
            .then(helpers.expect200);
        },

        /**
         * Authenticate the user using facebook
         */
        loginFacebook: function (facebookData) {
          return helpers
            .userApiCall('post', '/login_facebook', {
              // TODO: uncomment this line
              facebook_token: facebookData.authResponse.accessToken
              // TODO: remove this line
              // facebook_token: 'EAAOlGomqNZCEBAP0qxpxsF24LHj0U6f31n9pHfM2dWyBHSlW5P2gD3ZCkragDChLxZC2vmOdOeJxS6NJDHQiRel6schIQaC8aapfzlO9NzMTYBMVug27xr6m9XOW70nHpaiycsqGl33djVykZARc10PIg09g4gANZCa2NcRYKga0F32fZBH7PFNJs54rNf89MZD'
            })
            .then(helpers.expect200);
        },

        /**
         * Logout the user
         * @param  {Object} userData  an hash containing the 'accessToken'
         * @return {Promise}          A promise with the response
         */
        logout: function (userData) {
          return helpers
            .userApiCall('post', '/logout', {
              access_token: userData.accessToken,
              anonymous_user: 'siteweb'
            })
            .then(helpers.expect200);
        },

        /* EVENTS API */

        /**
         * List the events of the current user
         * @return {Promise} A promise fulfilled with the event list
         */
        listEvents: function () {
          return helpers
            .eventsApiCall('get', '/events/' + models.currentUser.uuid)
            .then(helpers.expect200);
        },

        /**
         * Create a new event
         * @return {Promise} A promise fulfilled with the event created
         */
        createEvent: function (eventData) {
          return helpers
            .eventsApiCall('post', '/events/add/' + models.currentUser.uuid, eventData)
            .then(helpers.expect200);
        },

        /**
         * Update an event
         */
        updateEvent: function (eventId, eventData) {
          return helpers
            .eventsApiCall('post', '/events/' + eventId + '/edit/' + models.currentUser.uuid, eventData)
            .then(helpers.expect200);
        },

        /**
         * Destroy an event
         */
        destroyEvent: function (eventData) {
          return helpers
            .eventsApiCall('delete', '/events/' + eventData.evenId + '/remove/' + models.currentUser.uuid)
            .then(helpers.expect200);
        },

        /**
         * Show an event
         * @return {Promise} A promise fulfilled with the event requested
         */
        showEvent: function (eventId) {
          return helpers
            .eventsApiCall('get', '/event/' + eventId + '/' + models.currentUser.uuid)
            .then(helpers.expect200);
        },

        /**
         * List the active regions
         */
        listRegions: function () {
          return helpers
            .eventsApiCall('get', '/regions/' + models.currentUser.uuid, null, null, true)
            .then(helpers.expect200);
        },

        /* TEAMS API */

        /**
         * List the teams of an event
         * @param  {Number} eventId The id of the event
         */
        listTeams: function (eventId) {
          return helpers
            .teamsApiCall(eventId, 'get', '/' + models.currentUser.uuid)
            .then(helpers.expect200);
        },

        /**
         * Add a group of teams to an event
         * @param {Number} eventId   The id of the event to add the teams to
         * @param {Object} teamsData An object containing the informations about the teams we want to create
         */
        addGroupTeams: function (eventId, teamsData) {
          return helpers
            .teamsApiCall(eventId, 'post', '/add_group_teams/' + models.currentUser.uuid, teamsData)
            .then(helpers.expect200);
        },

        /* MATCHES API */


        /**
         * Create a new event
         * @return {Promise} A promise fulfilled with the event created
         */
        createMatchesCalendar: function (eventId) {
          return helpers
            .matchesApiCall(eventId, 'post', '/match/create_calendar/' + models.currentUser.uuid)
            .then(helpers.expect200);
        },

        /**
         * List the matches of an event for a given day
         * @return {Promise} A promise fulfilled with the matches data
         */
        listMatches: function (eventId, day) {
          return helpers
            .matchesApiCall(eventId, 'get', '/day/' + day + '/matches/' + models.currentUser.uuid)
            .then(helpers.expect200);
        }

      };

      return frispoApi;
   });

'use strict';

angular.module('frispo')
  /**
   * Error Manager.
   * Handles common HTTP error messages
   */
  .factory('errorManager', function (toastr, $log, $state, $q, longTermMemory, models) {
    var errorManager = function(err) {
      // log every errors
      $log.error(err);

      // Unwrap HTTP errors and take actions if needed
      if(angular.isObject(err) && err.status){
        switch(err.status){
          case 401:
            $state.go('main.logout');
            err = err.data;
            break;
          case 403:
            $state.go('main.home');
            err = err.data;
            break;
          case 422:
            err = err.data;
            break;
          case 404:
            err = err.data;
            break;
          case 413:
            err = 'Il file che stai cercando di caricare  troppo grande';
            break;
          case -1:
            err = 'Il server non  raggiungibile o non sei connesso ad Internet';
            break;
          default:
            break;
        }
      }

      var getErrorString = function (err) {
        if(angular.isString(err)){
          return err;
        }
        else if(angular.isObject(err) && (err.full_messages || err.error_description || err.errors || err.data || err.msg || err.error)){
          return getErrorString(err.full_messages || err.error_description || err.errors || err.data || err.msg || err.error );
        }
        else if(angular.isArray(err)){
          var myErr = [];
          for (var i = 0; i < err.length; i++) {
            myErr[i] = getErrorString(err[i]);
          }
          return getErrorString(myErr.join('<br>'));
        }
        // Don't manage different kinds of error
        else{
          longTermMemory.oblivion(); // Forget everything. We don't want the app to be broken forever!
          models.reset(); // Reset all the models. We don't want the app to be broken forever!
          return 'Si  verificato un errore imprevisto. Si prega di riprovare pi tardi.';
        }
      }

      toastr.error(getErrorString(err), 'Errore');

      return $q.reject(err);
    };

    return errorManager;
  });

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('datepicker', datepicker);

  /** @ngInject */
  function datepicker() {
    var directive = {
      restrict: 'A',
      link: datepickerLink,
      scope: {
        datepicker: '@',
        model: '='
      }
    };

    return directive;

    /** @ngInject */
    function datepickerLink(scope, el) {
      scope.watchInput = function () {
        scope.$watch('model', function () {
          scope.drp.elementChanged();
        });
      }

      if(scope.datepicker === 'range'){
        scope.drp = el.daterangepicker({
          autoUpdateInput: false,
          locale: {
            format: 'DD/MM/YYYY',
            separator: ' -> '
          }
        }).data('daterangepicker');
        el.on('apply.daterangepicker', function (ev, picker) {
          scope.$apply(function () {
            scope.model = picker.startDate.format('DD/MM/YYYY') + ' -> ' + picker.endDate.format('DD/MM/YYYY');
          })
        })
        el.on('cancel.daterangepicker', function () {
          scope.$apply(function () {
            scope.model = '';
          })
        })
        scope.watchInput();
      }
      else{
        scope.drp = el.daterangepicker({
          autoUpdateInput: false,
          singleDatePicker: true,
          locale: {
            format: 'DD/MM/YYYY',
            separator: ' -> '
          }
        }).data('daterangepicker');
        el.on('apply.daterangepicker', function (ev, picker) {
          scope.$apply(function () {
            scope.model = picker.startDate.format('DD/MM/YYYY');
          })
        })
        el.on('cancel.daterangepicker', function () {
          scope.$apply(function () {
            scope.model = '';
          })
        })
        scope.watchInput();
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('compareTo', compareTo);

  /** @ngInject */
  function compareTo() {
    var directive = {
      restrict: 'A',
      require: 'ngModel',
      scope: {
        otherModelValue: '=compareTo'
      },
      link: compareToLink
    };

    return directive;

    /** @ngInject */
    function compareToLink(scope, element, attrs, ngModel) {
      ngModel.$validators.compareTo = function (modelvalue) {
        return modelvalue == scope.otherModelValue;
      }
      scope.$watch('otherModelValue', function () {
        ngModel.$validate();
      })
    }
  }

})();

'use strict';

angular.module('frispo')
  /**
   * User models manager
   */
   .factory('users', function (frispoApi, errorManager, models, longTermMemory, resPipe, toastr, $state, $q, ezfb, $log) {

      var users = {
        /**
         * Register a new user
         * @param  {Object} userData an object containing 'email' and 'password'
         * @return {Promise}         a promise with the authentication data for the user
         */
        register: function (userData) {
          return frispoApi
            .register(userData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(function (res) {
              res.email = userData.email;
              return res;
            })
            .then(resPipe.camelize)
            .then(resPipe.injectUserDefaults)
            .then(resPipe.updateCurrentUser)
            .then(resPipe.memorizeCurrentUser)
            .then(function (res) {
              $log.info('Frispo registration');
              return res;
            })
            .catch(errorManager);
        },

        /**
         * Login a user
         * @param  {Object} userData an object containing 'email' and 'password'
         * @return {Promise}         a promise with the authentication data for the user
         */
        login: function (userData) {
          return frispoApi
            .login(userData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(function (res) {
              res.email = userData.email; // Add the email from the input as it's missing from the response
              return res;
            })
            .then(resPipe.camelize)
            .then(resPipe.injectUserDefaults)
            .then(resPipe.updateCurrentUser)
            .then(resPipe.memorizeCurrentUser)
            .then(users.onLoggedIn)
            .catch(errorManager);
        },

        /**
         * Logout a user
         * @param  {Object} userData an object containing the user data
         * @return {Promise}          a promise that will be fullfilled after the logout
         */
        logout: function (userData) {
          if(userData.loggedIn){
            return frispoApi
              .logout(models.currentUser)
              .then(function (res) {
                toastr.success('Succesfully logged out', 'Logout');
                $log.info('Frispo logout');
                return res;
              })
              .catch(errorManager)
              .finally(function() {
                models.currentUser = {};
                longTermMemory.forget('currentUser');
                $state.go('main.login');
              })
              .then(function () {
                ezfb.logout();
              });
          }
          return $q.resolve(userData);
        },

        /**
         * Login a user to frispo's server using the facebook login() response data
         * @param  {Object} facebookData The object returned by the Facebook SDK after a succesful login
         * @return {Promise}             A promise fulfilled with the authentication data for the user,
         */
        loginFacebook: function (facebookData) {
          return frispoApi
            .loginFacebook(facebookData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.injectUserDefaults)
            .then(resPipe.updateCurrentUser)
            .then(resPipe.memorizeCurrentUser)
            .then(users.onLoggedIn)
            .catch(errorManager);
        },

        /**
         * This function is used as a callback whenever a user has been succesfully logged in
         * @param  {Object} userData The object containing the user data
         * @return {Object}          The same userData, useful for promise chaining
         */
        onLoggedIn: function (userData) {
          toastr.success("Benvenuto, " + (userData.displayName) + "!", "Accesso effettuato")
          if($state.current.name === 'main.login' || $state.current.name === 'main.register'){
            $state.go('main.admin.dashboard');
          }
          $log.info('Frispo login');
          return userData;
        },

        /**
         * Backup user data and setup the anonymous user for testing purposes
         */
        setAnonymousUser: function (res) {
          models.bkp =  {
            uuid: models.currentUser.uuid,
            accessToken: models.currentUser.accessToken
          }
          models.currentUser.uuid = 'anonimo';
          models.currentUser.accessToken = '4e66c966e021918f0b088ddc929847f7'
          return $q.resolve(res);
        },

        /**
         * Restore user backup
         */
        unsetAnonymousUser: function (res) {
          models.currentUser.uuid = models.bkp.uuid;
          models.currentUser.accessToken = models.bkp.accessToken;
          delete models.bkp;
          return $q.resolve(res);
        }
      };

      return users;
   });

