angular.module('frispo')
  /**
   * Event models manager
   */
   .factory('teams', function (frispoApi, errorManager, resPipe, $q, models, utils) {

      var teams = {
        /**
         * List the teams of the current event
         */
        list: function () {
          return frispoApi
            .listTeams(models.currentEvent.evenId)
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.updateTeams)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager)
        },

        /**
         * Create a group of teams and add it to an event
         */
        addGroup: function (teamsData) {
          return frispoApi
            .addGroupTeams(models.currentEvent.evenId, teamsData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .catch(errorManager);
        },

        /**
         * Translate data from a team form into an teamsData object
         * @param  {Object} formData The form input
         * @return {Promise}         A promise fulfilled with the teamsData object
         */
        form2Teams: function (formData) {
          var teamsData = { extra: [] };
          angular.forEach(formData.teams, function (team) {
            var teamData = {};
            var teamTime = utils.str2Time(team.time);
            if(team.captain) teamData.team_captain = team.captain;
            if(teamTime) teamData.team_time = teamTime;
            if(team.name) teamData.team_name = team.name;
            if(team.phone) teamData.team_mobile_phone = team.phone;
            if(team.place) teamData.team_place = team.place;
            if(team.round) teamData.team_groups = team.round;
            if(team.desc) teamData.team_description = team.desc;
            teamsData.extra.push(teamData);
          });
          return $q.resolve(teamsData);
        }
      };

      return teams;
   });

